.cv-page {

    height: 100vh;
    width: 85%;
    @include center-div;
    @include scroll-snap-align-start;
    float: right;

    background-color: $color2;

    &__cv-title {
        font-size: 5rem;
        margin-top: 100px;
        color: $color1;
    }

    &__container {
        width: 100%;
        margin-top: 100px;
        display: flex;
        justify-content: center;
        align-items: center;

        &__download-box {
            height: 400px;
            border: 5px solid $color4;
            display: flex;
            align-items: center;
            padding: 30px;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            transform: translateY(10px);
            transition: all 1s;
        
            &:hover {
                -webkit-box-shadow: 21px 24px 0px -7px rgba(58, 123, 213, 0.5);
                -moz-box-shadow: 21px 24px 0px -7px rgba(58, 123, 213, 0.5);
                box-shadow: 21px 24px 0px -7px rgba(58, 123, 213, 0.5);
                transform: translateY(-10px);
                transition: all 0.5s;
            }

        &__download-link {
            transition: all .5s;
            text-decoration: none;
            color: $color1;

            &:hover {
                color: $color4;
                transform: translateY(-4px);
                
            }

            &__text {
                padding-bottom: 40px;
                font-size: 3rem;
                color: $color1;
                text-align: center;
                font-weight: bold;
                white-space: pre-wrap;
            }
        }
        }
    }
}

/* MEDIA QUERIES */

/* IPAD */
@media screen and (max-width: 1110px) {
    .cv-page {
        &__container {
            &__download-box {
                height: auto;
                margin-top: -50px;
    
            &__download-link {
                &__text {
                    font-size: 2.5rem;
                }
            }
            }
        }
    }
}

/* PHONE */
@media screen and (max-width: 627px) {
    .cv-page {
        width: 100%;

        &__container {
            &__download-box {    
            &__download-link {
                &__text {
                    font-size: 1.5rem;
                }
            }
            }
        }
}
}
