.btn {
    &__nav-bar-btn {
        font-size: 2.5rem;
        cursor: pointer;
        border-style: none;
        background-color: transparent;
        flex: 1;
        color: $color2;
        border-bottom: 1px solid transparent;
        transition: all .2s;

        &:focus{ outline-color: rgba(241, 241, 241, 0) }
        &:hover { 
            border-bottom: 1px solid $color2;
            transform: translateY(-3px);
            
        }

        &__scroll-to-top-btn {
            position: absolute;
            font-size: 5rem;
            color: $color5;
        }
    }

    &-contact-submit {
        width: 100px;
        font-size: 2.5rem;
        padding: 1rem;
        cursor: pointer;
        border: 3px solid $color1;
        border-radius: 5px;
        background-color: transparent;
        flex: 1;
        color: $color1;
        transition: all .2s;

        &:hover {
            background-color: rgba(58, 123, 213, 0.5);
            transform: translateY(-2px);
            box-shadow: 0 5px rgb(155, 155, 155);
        }

        &__disabled {
            pointer-events: none;
            color: rgb(146, 146, 146);
            border: none;
            transition: all .5s;
        }
    }

    &-view-code {
        margin-top: 20px;
        font-size: 1.2rem;
        padding: 1rem;
        cursor: pointer;
        border: 3px solid $color1;
        border-radius: 5px;
        background-color: transparent;
        color: $color1;
        transition: all .2s;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        width: 140px;

        &:hover {
            background-color: rgba(58, 123, 213, 0.5);
            transform: translateY(-2px);
            box-shadow: 0 5px rgb(155, 155, 155);
        }

        a:visited { text-decoration: none; color:$color1; }
        a:hover { text-decoration: none; color:$color1; }
        a:focus { text-decoration: none; color:$color1; }
        a:hover, a:active { text-decoration: none; color:$color1; }
    }

    &-view-demo {
        margin-top: 20px;
        font-size: 1.2rem;
        padding: 1rem;
        cursor: pointer;
        border: 3px solid $color1;
        border-radius: 5px;
        background-color: transparent;
        color: $color1;
        transition: all .2s;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        width: 140px;

        &:hover {
            background-color: rgba(58, 123, 213, 0.5);
            transform: translateY(-2px);
            box-shadow: 0 5px rgb(155, 155, 155);
        }

        a:visited { text-decoration: none; color:$color1; }
        a:hover { text-decoration: none; color:$color1; }
        a:focus { text-decoration: none; color:$color1; }
        a:hover, a:active { text-decoration: none; color:$color1; }
    }
}

/* MEDIA QUERIES */

/* IPAD */
@media screen and (max-width: 1110px) {

    .btn {
        &__nav-bar-btn {
            font-size: 1.5rem;
            display: flex;
            align-items: center;
            flex-direction: column;
            justify-content: center;
        }
    }
}

/* PHONE */
@media screen and (max-width: 627px) {
    .btn {
        &__nav-bar-btn {
            font-size: 1.1rem;
            cursor: pointer;
            border-style: none;
            background-color: transparent;
            flex: 1;
            color: $color2;
            border-bottom: 1px solid transparent;
            transition: all .2s;
        }
    }
}