.contact-page {

    height: 100vh;
    width: 85%;
    @include center-div;
    @include scroll-snap-align-start;
    float: right;
    position: relative;
    overflow: hidden;
    
    background-color: $color2;

    &__contact-title {
        font-size: 5rem;
        margin-top: 100px;
        color: $color1;
        padding-bottom: 100px;
    }

    .contact-form {
        width: 70%;
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;
        border: 5px solid rgba(58, 123, 213, 1);
        transform: translateY(2px);
        transition: all 1s;
        padding: 20px;
        &:hover {
            -webkit-box-shadow: 21px 24px 0px -7px rgba(58, 123, 213, 0.5);
            -moz-box-shadow: 21px 24px 0px -7px rgba(58, 123, 213, 0.5);
            box-shadow: 21px 24px 0px -7px rgba(58, 123, 213, 0.5);
            transform: translateY(-2px);
            transition: all 0.5s;
        }
    }

    &__form-list {
        list-style: none;
        display: flex;
        flex-direction: column;
        width: 100%;
    }

    &__form {

        &__list-item {
            padding: 5px;
            display: flex;
            flex-direction: column;
        }
        
        &__name-input {
            background-color: transparent;
            font-size: 2.5rem;
            width: auto;
            padding: 12px 20px;
            margin: 8px 0;
            border: none;
            border-bottom: 2px solid $color1;
            &:focus {
                outline: none;
            }
        }

        &__email-input {
            background-color: transparent;
            font-size: 2.5rem;
            width: auto;
            padding: 12px 20px;
            margin: 8px 0;
            border: none;
            border-bottom: 2px solid $color1;

            &:focus {
                outline: none;
            }
        }

        &__message-input {
            background-color: #F3F3F3;
            font-size: 2.5rem;
            width: auto;
            padding: 12px 20px;
            margin: 8px 0;
            border: none;
            border-bottom: 2px solid $color1;
            width:100%;
            resize:none; 
            max-height:250px; 
            min-height:100px; 

            &:focus {
                outline: none;
            }
        }

        &__text {
            font-size: 2.5rem;
            font-weight: bold;
            color: $color1;
        }

    }
}

.overlay {
    background-color: rgba(0, 0, 0, 0.8);
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    position: absolute;
    opacity: 0;
    pointer-events: none;

    &__message-box {
    background-color: $color2;
    width: 700px;
    height: 500px;
    display: flex;
    align-items: center;
    z-index: 100;
    justify-content: center;
    border-radius: 50px;
    border: 5px solid $color4;
    flex-direction: column;

        h1 {
            padding-bottom: 50px;
            color: $color1;
            font-size: 4rem;
        }
    }

    &__active {
        opacity: 100;
        pointer-events: all;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: all .5s;    
    }
}

.btn-contact-wrapper {
    @include center-div;
    width: 100%;
    height: 100%;
}

@media screen and (max-width: 1110px) {

    .contact-form {
        margin-top: 60px;
    }
}

/* PHONE */
@media screen and (max-width: 627px) {
    .contact-page {
        max-height: 100vh;
        width: 100%;

        &__contact-title {
            font-size: 5rem;
            margin-top: 100px;
            color: $color1;
            padding-bottom: 0;
        }
}
    .contact-form {
        margin-top: 40px;
    }

    .overlay {
        background-color: rgba(0, 0, 0, 0.8);
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        position: absolute;
        opacity: 0;
        pointer-events: none;
    
        &__message-box {
        background-color: $color2;
        width: 700px;
        height: 400px;
        display: flex;
        align-items: center;
        z-index: 100;
        justify-content: center;
        border-radius: 50px;
        border: 5px solid $color1;
    
            h1 {
                color: $color1;
                font-size: 2rem;
            }
        }
    
        &__active {
            opacity: 100;
            pointer-events: all;
            display: flex;
            align-items: center;
            justify-content: center;
            transition: all .5s;    
        }
    }
}