.landing-page {

    height: 100vh;
    width: 85%;
    @include center-div;
    justify-content: center;
    float: right;
    background: $color2;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #FFFFFF, $color2);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #FFFFFF, $color2); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    text-align: center;


    &__name-title {
        font-size: 15rem;
        color: $color1;
        font-weight: bold;
        z-index: 10;
    }

    &__job-title {
        font-size: 5rem;
        margin-top: 25px;
        color: $color1;
        font-weight: bold;
        z-index: 10;
    }

    &__scroll-to-top-btn {
    height: 90vh;
    position: fixed;
    left: 90%;
    margin-top: 80px;
    }
}

/* MEDIA QUERIES */

/* IPAD */
@media screen and (max-width: 1110px) {
    .landing-page {
        &__name-title {
            font-size: 7rem;
        }

        &__job-title {
            font-size: 2rem;
        }
    }
}
/* PHONE */
@media screen and (max-width: 627px) {
    .landing-page {
        width: 100%;
        overflow: hidden;
        &__name-title {
            font-size: 5rem;
        }
        &__job-title {
            font-size: 1.5rem;
        }
    }
}

@keyframes cube {
    from {
        transform: scale(0) rotate(0deg) translate(-50%, -50%);
        opacity: 1;
    }
    to {
        transform: scale(20) rotate(960deg) translate(-50%, -50%);
        opacity: 0;
    }
}

.background {
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    margin: 0;
    padding: 0;
    background: #ffffff;
    overflow: hidden;
}
.background li {
    position: absolute;
    top: 80vh;
    left: 45vw;
    width: 10px;
    height: 10px;
    border: solid 1px #e5e5e5;
    color: transparent;
    transform-origin: top left;
    transform: scale(0) rotate(0deg) translate(-50%, -50%);
    animation: cube 7s ease-in forwards infinite;
}
