.portfolio-page {
    min-height: 100vh;
    width: 85%;
    @include center-div;
    float: right;
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
    
    background-color: $color2;

    &__portfolio-title {
        font-size: 5rem;
        margin-top: 15px;
        margin-bottom: -10px;
        color: $color1;
    }
}

.portfolio-gallery {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
    column-gap: 20px;
    row-gap: 20px;
    padding: 30px;

    &__box {
    position: relative;
    border: 5px solid $color4;
    background-color: rgba(240, 247, 244, 0.8);
    transform: translateY(10px);
    transition: all 1s;

    &:hover {
        -webkit-box-shadow: 21px 24px 0px -7px rgba(58, 123, 213, 0.5);
        -moz-box-shadow: 21px 24px 0px -7px rgba(58, 123, 213, 0.5);
        box-shadow: 21px 24px 0px -7px rgba(58, 123, 213, 0.5);
        transform: translateY(-10px);
        transition: all 0.5s;
    }
    

        &__info {
            display: flex;
            justify-content: space-evenly;
            align-items: center;
            flex-direction: column;
            height: auto;
            width: 100%;
            padding: 30px;
            border-top: 5px solid $color4;
            
            h1 {
                font-size: 2.8rem;
                color: $color1;
                margin-top: 10px;
                margin-bottom: 10px;
            }

            p{
                font-size: 1.8rem;
                color: $color1;
                margin-top: 10px;
                margin-bottom: 10px;
            }

            &__language-icons {
                display: flex;
                width: 200px;
                align-items: center;
                justify-content: space-evenly;
            }

        }

    }

    img {
        width: 100%;
    }
}

/* PHONE */
@media screen and (max-width: 627px) {
    .portfolio-page {
        width: 100%;

        &__portfolio-title {
            padding: 10px;
        }
    }

    .portfolio-gallery {
        grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
        padding: 0;

        &__box {
            position: relative;
            border: 5px solid $color1;
            background-color: rgba(240, 247, 244, 0.8);
            
        
                &__info {
                    display: flex;
                    justify-content: space-evenly;
                    align-items: center;
                    flex-direction: column;
                    height: auto;
                    border-bottom-left-radius: 25px;
                    border-bottom-right-radius: 25px;
                    width: 100%;
                    padding: 30px;
                    border-top: 5px solid $color1;
                    
                    h1 {
                        font-size: 1.9rem;
                        color: $color1;
                        margin-top: 10px;
                        margin-bottom: 10px;
                    }
        
                    p{
                        font-size: 1.3rem;
                        color: $color1;
                        margin-top: 10px;
                        margin-bottom: 10px;
                    }
        
                    &__language-icons {
                        display: flex;
                        width: 200px;
                        align-items: center;
                        justify-content: space-evenly;
                    }
        
                }
        
            }
        
    }
}

.portfolio-gallery__box__info__buttons{
    display: flex;
    width: 300px;
    justify-content: space-between;
}

