.about-page {

    height: 100vh;
    width: 85%;
    @include center-div;
    @include scroll-snap-align-start;
    float: right;
    background-color: $color2;

    &__about-title {
        font-size: 5rem;
        margin-top: 50px;
        color: $color1;
    }

    &__images{
        margin-top: 25px;
        &__portrait {
            height: 180px;
            width: 180px;
            object-fit: cover;
            scale: 0.5;
            border: 5px solid $color4;
            transform: translateY(10px);
            transition: all 1s;
        
            &:hover {
                -webkit-box-shadow: 21px 24px 0px -7px rgba(58, 123, 213, 0.5);
                -moz-box-shadow: 21px 24px 0px -7px rgba(58, 123, 213, 0.5);
                box-shadow: 21px 24px 0px -7px rgba(58, 123, 213, 0.5);
                transform: translateY(-10px);
                transition: all 0.5s;
            }
            
        }
    }

    &__bio-wrapper {
        width: 70%;
        margin-top: 25px;
        padding: 25px;
        border: 5px solid $color4;
        transform: translateY(10px);
        transition: all 1s;
    
        &:hover {
            -webkit-box-shadow: 21px 24px 0px -7px rgba(58, 123, 213, 0.5);
            -moz-box-shadow: 21px 24px 0px -7px rgba(58, 123, 213, 0.5);
            box-shadow: 21px 24px 0px -7px rgba(58, 123, 213, 0.5);
            transform: translateY(-10px);
            transition: all 0.5s;
        }
        &__title {
            font-weight: bold;
            font-size: 4.8rem;
            text-align: center;
            margin-bottom: 25px;
        }

        &__bio{
        font-size: 2.5rem;
        padding-bottom: 25px;
        }
    }

    &__social-icons {
        display: flex;
        justify-content: space-around;
        width: 150px;
        margin-top: 25px;
        border: 5px solid $color4;
        padding: 10px;
        transform: translateY(2px);
        transition: all 1s;
    
        &:hover {
            -webkit-box-shadow: 21px 24px 0px -7px rgba(58, 123, 213, 0.5);
            -moz-box-shadow: 21px 24px 0px -7px rgba(58, 123, 213, 0.5);
            box-shadow: 21px 24px 0px -7px rgba(58, 123, 213, 0.5);
            transform: translateY(-2px);
            transition: all 0.5s;
        }

        &__icon {
            color: $color1;
            transition: all .5s;

            &:hover {
                color: $color4;
                transform: translateY(-2px);
            }
        }

    }
}

/* MEDIA QUERIES */

/* IPAD */
@media screen and (max-width: 768px) {

    .about-page {

        &__about-title {
            margin-top: 10px;
        }

        &__social-icons {
            margin-top: 40px;
        }

        
    }
}

/* PHONE */
@media screen and (max-width: 627px) {
    .about-page {
        width: 100%;
        height: 100%;

        &__about-title {
            margin: 0;
            padding-top: 80px;
        }

        &__social-icons {
            margin-top: 25px;
        }

        &__bio-wrapper {
            width: 90%;

            &__title {
                font-size: 1.8rem;
            }
    
            &__bio{
            font-size: 1.4rem;
            }
        }
}

        
}